
import React, { useState } from "react";
import { compileCode } from "../Compiler/compiler";
import { CodeArea } from "../general/CodeArea";
import { cpu } from "./cpu-panel";

import "./compiler.css";

const defaultCode = `start: 40
end: 80

setup:
		x = start
		y = end
	loop:
		x - y = 0? exit
				M0(x) = x
				x = x + 1
		c = loop

exit:
	c = exit`;



export function CompilerPanel(props) {

	const [code, setCode] = useState(defaultCode);

	function saveToClipboard() {
		const results = compileCode(code);
		if (results.errors.length == 0) {
			let text = " ";
			for (const number of results.code)
				text += number.toString(16).padStart(4, "0") + " ";
			navigator.clipboard.writeText(text.trim());
		} else {
			navigator.clipboard.writeText("pending code errors");
		}
	}

	function saveToM0() {
		const results = compileCode(code);
		if (results.errors.length == 0) {
			for (const i in results.code)
				cpu.peripherals[cpu.reg.M[0]].set(i, results.code[i]);
		}
	}

	return (
		<div>
			<div id="myHeader" className="panel-container-row">
				Compile and save to: &nbsp;
				<div className="panel-button" onClick={saveToClipboard}>Clipboard</div>
				<div className="panel-button" onClick={saveToM0}>M0</div>
			</div>
			<CodeArea className="compiler-codeArea" value={code} update={setCode} />
		</div>
	);
}