
import React, { useState } from 'react';

import "./memory.css";

function numToTxt(n) {
	return n.toString(16).toUpperCase().padStart(4, "0");
}

export function MemoryPanel(props) {
	const [_, update] = useState({});
	const forceUpdate = () => update({});
	props.content.peripheral.onChange = forceUpdate;

	const [page, setPage] = useState(0);
	let address = page * 32 * 8;
	const text = [];

	for (let y = 0; y < 32; ++y) {
		const line = numToTxt(address) + ":";
		for (let x = 0; x < 8; ++x) {
			line += " " + numToTxt(props.content.peripheral.get(address));
			++address;
		}
		text.push(<div key={y} className="memory-line">{line}</div>);
	}

	function loadData() {
		navigator.clipboard.readText().then((result) => {
			if (result != null) {
				if (typeof result === "string") {
					const numbers = result.split(";")[0].match(/[ABCDEF\d]+(?=\s+|$)/gi);
					for (const n of numbers) { // check
						if (isNaN(Number("0x" + n))) return;
					}
					let i = 0;
					for (const n of numbers) { // load
						props.content.peripheral.set(page * 32 * 8 + i, Number("0x" + n));
						++i;
					}
				}
			}
		});
	}

	return (
		<div>
			<div className="panel-container-row">
				<div className="panel-button" onClick={loadData}>Load Data</div>
				<div className="panel-button" onClick={() => { setPage(page == 0 ? 0xff : page - 1); }}>Back</div>
				<div className="panel-button" onClick={() => { setPage(page == 0xff ? 0 : page + 1); }}>Next</div>
			</div>
			<div className="panel-container-row">
				<div className="memory-content">
					{text}
				</div>
			</div>
		</div>
	);
}