import CodeMirror from 'codemirror';
import 'codemirror/addon/mode/simple.js';
import 'codemirror/addon/lint/lint';
import { compileCode } from '../../Compiler/compiler';

CodeMirror.defineSimpleMode("assembly", {
	start: [
		{
			regex: /max(?=\s*\?)|[-+]?(0x[a-f\d]+|0b[01]+|(?:\d+))/i,
			token: "number"
		},
		{
			regex: /\w*\s*\:/,
			token: "type",
		},
		{
			regex: /[\+\-\^|&]|\<\<|\>\>/,
			token: "atom",
		},
		{
			regex: /[=>\?]/,
			token: "operator",
		},
		{
			regex: /(?:x|y|c)\b/,
			token: "variable",
		},
		{
			regex: /[\(\)]|(?:M0|M1|M2|M3|M4|M5|M6|M7)\b/,
			token: "variable-3",
		},
		{
			regex: /\w+/,
			token: "variable-2",
		},
	],
	comment: [
		{ regex: /.*?\*\//, token: "comment", next: "start" },
		{ regex: /.*/, token: "comment" }
	],
	meta: {
		dontIndentStates: ["comment"],
		lineComment: "//"
	}
});

CodeMirror.registerHelper("lint", "assembly", function (text) {
	var found = [];
	
	const result = compileCode(text);
	const lines = text.split("\n");
	
	for (const errorLine of result.errors) {
		const numLine = parseInt(errorLine);
		found.push({
			from: CodeMirror.Pos(numLine, lines[numLine].search(/\S|$/)),
			to: CodeMirror.Pos(numLine, lines[numLine].length),
			message: "" + numLine,
		});
	}
	return found;
});