import { encodeInst, NAME } from "./instNames";


const asm_keyWords = "=<>+:?&!()\\-\\^\\|"
const asm_keyWords_regExp = new RegExp(`0\\s*\\?|max\\s*\\?|<<\\s*1|>>\\s*1|[${asm_keyWords}]|[^${asm_keyWords}\\s]+`, "g");
const only_KeyWord_regExp = new RegExp(`[${asm_keyWords}]`);

function canBeLabel(word) {
	return !(
		only_KeyWord_regExp.test(word) ||
		["x", "y", "c", "#", "M0", "M1", "M2", "M3", "M4", "M5", "M6", "M7"].includes(word)
	);
}

function locateKeyWords(src) {
	if (typeof src == "string") src = src.split("\n");

	const keyWords = [];

	for (const line of src) {
		keyWords.push(line.match(asm_keyWords_regExp));
	}
	return keyWords;
}

function parseNum(word) {
	let num = Number(word);
	if (num != parseInt(word)) return NaN;
	if (num > 0xFFFF) return NaN;
	if (num < -0xFFFF / 2) return NaN;
	return num;
}

function getLabels(keyWords, codeOffset = 3) {
	const errors = [];
	const labels = new Map();
	let codeLineCount = codeOffset;

	function newLabel(name, val, lineIndex) {
		if (canBeLabel(name) && !labels.has(name) && !isNaN(val)) labels.set(name, val);
		else errors.push(lineIndex);
	}

	for (const lineIndex in keyWords) {
		const line = keyWords[lineIndex];
		if (line == null) continue;

		if (line.includes(":")) {
			if (line.length == 3) newLabel(line[0], parseNum(line[2]), lineIndex);
			else if (line.length == 2) newLabel(line[0], codeLineCount, lineIndex);
			else errors.push(lineIndex);
		} else {
			codeLineCount += 2;
		}
	}
	return [ labels, errors ];
}

function translateCode(keyWords, labels) {
	const errors = [];
	const code = [0, 0, 0, 0];
	const testError = (error, lineIndex) => { 
		if (error) {
			errors.push(lineIndex);
			code.push(-1);
			code.push(-1);
		} 
		return error;
	}
	
	for (const lineIndex in keyWords) {
		const line = keyWords[lineIndex];
		if (line == null) continue;
		if (line.includes(":")) continue;
		
		const param = getParamLine(line, labels);
		if (testError(isNaN(param), lineIndex)) continue;
				
		line = replaceConstants(line, labels);
		
		const instCode = encodeLine(line);
		if (testError(isNaN(instCode), lineIndex)) continue;
		
		code.push(instCode);
		code.push(param);
	}
	
	return [ code, errors ];
}

function getParamLine(line, labels) {
	let param;
	for (const word of line) {
		let wordValue = parseNum(word);
		if (!wordValue) wordValue = labels.get(word);
		if (wordValue) {
			if (param && param != wordValue) return NaN;
			param = wordValue;
		}
	}
	return param || 0;
}

function replaceConstants(line, labels) {
	return line.map(word => {
		return (labels.has(word) || !isNaN(parseNum(word)))? "#" : word;
	});
}

function encodeLine(line) {
	// branch
	return (
		encodeBranch([...line]) ||
		encodeALU([...line]) ||
		encodeLoad(line) ||
		encodeStore(line)
	);
	
	// alu
	// load
	// store
}

function encodeBranch(line) {
	//a = 0? #
	//a > 0? #
	//a > max? #

	//a + b = 0? #
	//a + b > 0? #
	//a + b > max? #

	if (!(line.length == 4 || line.length == 6)) return NaN;
	
	
	if (line.length == 4) {
		line.splice(1, 0, "|");
		line.splice(2, 0, line[0]);
	}
	
	if (line[5] != "#") return NaN;
	
	let branch;
	const rightSide = line[4].replace(" ", "");
	const middle = line[3];
	if (middle == "=" && rightSide == "0?") branch = "zero";
	if (middle == ">" && rightSide == "0?") branch = "positive";
	if (middle == ">" && rightSide == "max?") branch = "overflow";
	
	if (!branch) return NaN;
	
	return encodeInst({
		branch,
		opcode: "branch",
		aluOp: line[1],
		a: line[0],
		b: line[2],
		dst: "x",
		i: "M0",
	});
	
}

function encodeALU(line) {
	// Mi = a + b
	// d = a + b
	// Mi = a
	// d = a
	// Mi = a <<\s*1
	// d = a <<\s*1
	if (!(line.length == 3 || line.length == 4 || line.length == 5)) return NaN;
	if (line[1] != "=") return NaN;
	
	if (line.length == 3) {
		line.push("|");
		line.push(line[2]);
	}
	else if (line.length == 4) {
		line[3] = line[3].substr(0, 2);
		line.push("x");
	}
	
	return encodeInst({
		branch: "true",
		opcode: "alu",
		aluOp: line[3],
		a: line[2],
		b: line[4],
		dst: line[0].startsWith("M")? "Mi" : line[0],
		i: line[0].startsWith("M")? line[0] : "M0",
	});
}

function encodeLoad(line) {
	// d = Mi(a)
	if (line.length != 6) return NaN;
	if (line[1] != "=" || line[3] != "(" || line[5] != ")") return NaN;
	
	return encodeInst({
		branch: "true",
		opcode: "load",
		aluOp: "|",
		a: line[4],
		b: "x",
		dst: line[0],
		i: line[2],
	}); 
}

function encodeStore(line) {
	// Mi(a) = b
	if (line.length != 6) return NaN;
	if (line[4] != "=" || line[1] != "(" || line[3] != ")") return NaN;
	
	return encodeInst({
		branch: "true",
		opcode: "store",
		aluOp: "|",
		a: line[2],
		b: "x",
		dst: line[5],
		i: line[0],
	});
}


export function compileCode(src) {
	const keyWords = locateKeyWords(src);
	const [ labels, labelsErrors ] = getLabels(keyWords);
	const [ code, codeErrors ] = translateCode(keyWords, labels);
	return { errors: [...labelsErrors, ...codeErrors], code };
}