export const NAME = {
	branch: {size: 2, options:["true", "zero", "positive", "overflow"]},
	opcode: {size: 2, options:["alu", "load", "store", "branch"]},
	aluOp: {size: 3, options:["+","-", "&", "!&", "|", "^", "<<", ">>"]},
	a: {size: 2, options:["x", "y", "c", "#"]},
	b: {size: 2, options:["x", "y", "c", "#"]},
	dst: {size: 2, options:["x", "y", "c", "Mi"]},
	i: {size: 3, options:["M0","M1","M2","M3","M4","M5","M6","M7"]},
}

export function encodeInst(inst) {
	let num = 0;
	const names = ["branch", "opcode", "aluOp", "a", "b", "dst", "i"];
	for (const name of names) {
		const i = NAME[name].options.indexOf(inst[name]);
		if (i == -1) return NaN;
		num = num << NAME[name].size;
		num |= i;
	}
	return num;
}