

import React, { useState } from 'react';
import ReactDOM from "react-dom";

import "./Compiler/compiler.js";
import { Panel } from "./panel/panel.js";

import "./root.css";

export const panelsTypes = [
	{ title: "Compiler", type: "Compiler" },
	{ title: "0> Main Memory", type: "P-Memory" },
	{ title: "1> Memory", type: "P-Memory" },
	{ title: "2> Memory", type: "P-Memory" },
	{ title: "3> Memory", type: "P-Memory" },
	{ title: "4> Memory", type: "P-Memory" },
	{ title: "5> Memory", type: "P-Memory" },
	{ title: "6> Memory", type: "P-Memory" },
	{ title: "7> Memory", type: "P-Memory" },
	{ title: "8> Screen", type: "P-Screen" },
	{ title: "9> Keyboard", type: "P-Keyboard" },
	{ title: "10> Wave Gen", type: "P-Wave Gen" },
];
for (const i in panelsTypes) {
	panelsTypes[i].index = i;
}

const panelCPU = { type: "CPU", index: -1 };

function Main() {
	const [panelL, setPanelL] = useState(0);
	const [panelR, setPanelR] = useState(1);
	
	return (
		<div className="container">
			<Panel content={panelCPU} columns={5} />
			<Panel setPanel={setPanelL} content={panelsTypes[panelL]} columns={5 / 2} />
			<Panel setPanel={setPanelR} content={panelsTypes[panelR]} columns={5 / 2} />
		</div>
	);
}

ReactDOM.render(
	<Main />,
	document.getElementById("root")
);