import React from 'react';

import { CPUPanel } from "./cpu-panel";
import { MemoryPanel } from "./memory";
import { CompilerPanel } from './compiler';

import "./general-panel.css";

export function getCompByName(content) {
	switch(content.type) {
		case "CPU": return <CPUPanel content={content}/>;
		case "P-Memory": return <MemoryPanel content={content}/>;
		case "Compiler": return <CompilerPanel content={content}/>;
	}
	return <div></div>;
}