
import React from 'react';
import { panelsTypes } from '..';
import { Select } from '../general/select';


export function Header(props) {
	
	if (props.content.index != -1) {
		const options = panelsTypes.map(v=>{return v.title});
		return (
			<div className="header">
				<Select className="PanelSelector" defaultValue={props.content.index} options={options} 
					onChange={e=>{props.setPanel(e.target.value);}}/>
			</div>
		);
	}
	return (
		<div className="header">
			{props.content.type}
		</div>
	);
}