import { encodeInst } from "../Compiler/instNames";

function i16(n) {
	return n & 0xFFFF;
}

export class CPU {
	constructor(peripherals) {
		this.peripherals = peripherals;
		this.reg = {
			state: 0,
			interruptRequest: false,
			interrupt: false,
			inst: 0,
			param: 0,
			c: 3,
			x: 0,
			y: 0,
			M: [0, 0, 0, 0, 0, 0, 0, 0]
		}
	}
	step() {

		if (this.reg.state != 0 && this.reg.interrupt || this.reg.state == 0 && this.reg.interruptRequest) {
			this.reg.interrupt = true;
			this.reg.interruptRequest = false;
			const defInst = {
				branch: "true",
				opcode: "store",
				aluOp: "|",
				a: "#",
				b: "x",
				dst: "x",
				i: "M0",
			};
			switch (this.reg.state) {
				case 0:
					this.reg.inst = encodeInst(defInst);
					this.reg.param = 0;
					this.#execInst();
					++this.reg.state;
					break;
				case 1:
					defInst.b = "y";
					this.reg.inst = encodeInst(defInst);
					this.reg.param = 1;
					this.#execInst();
					++this.reg.state;
					break;
				case 2:
					defInst.b = "c";
					this.reg.inst = encodeInst(defInst);
					this.reg.param = 2;
					this.#execInst();
					++this.reg.state;
					break;
				case 3:
					defInst.opcode = "load";
					defInst.dst = "c";
					defInst
					this.reg.inst = encodeInst(defInst);
					this.reg.param = 3;
					this.#execInst();
					this.reg.state = 0;
					break;
				default:
					console.error("CPU state !!! (on interrupt)");
					break;
			}
		} else {
			this.reg.interrupt = false;
			switch (this.reg.state) {
				case 0:
					this.#fetchInst();
					++this.reg.state;
					break;
				case 1:
					this.#fetchParam();
					++this.reg.state;
					break;
				case 2:
					this.#execInst();
					this.reg.state = 0;
					break;
				default:
					console.error("CPU state !!!");
					break;
			}
		}
	}

	#fetchInst() {
		const M0 = this.reg.M[0];
		this.reg.c = i16(1+this.reg.c);
		this.reg.inst = this.peripherals[M0].get(this.reg.c);
	}
	
	#fetchParam() {
		const M0 = this.reg.M[0];
		this.reg.c = i16(1+this.reg.c);
		this.reg.param = this.peripherals[M0].get(this.reg.c);
	}

	#execInst() {
		const inst = this.#decodeInst();
		if (inst.opcode == 0) this.#execALU(inst);
		else if (inst.opcode == 1) this.#execLoad(inst);
		else if (inst.opcode == 2) this.#execStore(inst);
		else if (inst.opcode == 3) this.#execBranch(inst);
	}

	#execALU(inst) {
		this.#setReg(inst, this.#calcALU(inst));
	}

	#execLoad(inst) {
		const Mi = this.reg.M[inst.i];
		this.#setReg(inst, this.peripherals[Mi].get(this.#getReg(inst.a)));
	}

	#execStore(inst) {
		const Mi = this.reg.M[inst.i];
		this.peripherals[Mi].set(this.#getReg(inst.a), this.#getReg(inst.b));
	}

	#execBranch(inst) {
		const result = this.#calcALU(inst);
		if ((inst.branch == 1 && result != 0) ||
			(inst.branch == 2 && (result>>15) & 0b1 != 0) ||
			(inst.branch == 3 && (result>>16) & 0b1 != 0))
			return;
		this.reg.c = this.reg.param;
	}

	#calcALU(inst) {
		const a = this.#getReg(inst.a);
		const b = this.#getReg(inst.b);
		switch (inst.aluOp) {
			case 0: return i16(a + b);
			case 1: return i16(a - b);
			case 2: return i16(a & b);
			case 3: return i16(!(a & b));
			case 4: return i16(a | b);
			case 5: return i16(a ^ b);
			case 6: return i16(a << 1);
			case 7: return i16(a >> 1);
			default: throw new Error("calc ALU index overflow");
		}
	}

	#getReg(index) {
		switch (index) {
			case 0: return this.reg.x;
			case 1: return this.reg.y;
			case 2: return this.reg.c;
			case 3: return this.reg.param;
			default: throw new Error("get reg index overflow");
		}
	}
	#setReg(inst, value) {
		switch (inst.dst) {
			case 0: return this.reg.x = value;
			case 1: return this.reg.y = value;
			case 2: return this.reg.c = value;
			case 3: return this.reg.M[inst.i] = value;
			default: throw new Error("get reg dst index overflow");
		}
	}

	#decodeInst() {
		return {
			branch: (this.reg.inst >> 14) & 0b11,
			opcode: (this.reg.inst >> 12) & 0b11,
			aluOp: (this.reg.inst >> 9) & 0b111,
			a: (this.reg.inst >> 7) & 0b11,
			b: (this.reg.inst >> 5) & 0b11,
			dst: (this.reg.inst >> 3) & 0b11,
			i: (this.reg.inst >> 0) & 0b111,
		};
	}
}