
import React from 'react';
import { setPanelL } from '..';

export function Select(props) {
	const options = props.options.map((v, i) => {
		return (<option className={props.className} key={i} value={i}>{v}</option>);
	});
	return (
		<select className={props.className} defaultValue={props.defaultValue} onChange={props.onChange}>
			{options}
		</select>
	);
}