
import React from 'react';
import ReactDOM from "react-dom";



import "./panel.css";

import { Header } from './header';
import { getCompByName } from '../comps/panelCompSelector';

export function Panel(props) {
	const width = "calc(" + ((100) / props.columns) + "% - 6vw)";
	return (
	<div className="panel" style={{width}}>
		<Header content={props.content} setPanel={props.setPanel}/>
		<div className="panelContainer">
			{getCompByName(props.content)}
		</div>
	</div>
	);
}