
import React, { useState } from 'react';
import { panelsTypes } from '..';
import { CPU } from "../Simulation/CPU";
import { Memory } from '../Simulation/Memory';

import "./cpu-panel.css";


export let cpu;

function createCPU() {
	if (cpu) {
		for (const ph of cpu.peripherals)
			ph.forceUpdate();
	}
	
	const peripherals = [];
	for (const panel of panelsTypes) {
		if (!panel.type.startsWith("P-")) continue;
		panel.peripheral = new Memory();
		peripherals.push(panel.peripheral);
	}
	return new CPU(peripherals);
}


export function CPUPanel(props) {
	cpu = cpu || createCPU();
	const [delay, setDelay] = useState(100);
	const [runInterval, setRunInterval] = useState(NaN);
	const [_, forceUpdate] = useState(0);

	function onClickClk(count = 1) {
		for (let i = 0; i < count; ++i) cpu.step();
		forceUpdate({});
	}

	function createRunInterval(delayMs = delay) {
		delayMs = Number(delayMs);
		const multiplayer = Math.min(Math.max(Math.round(50 / delayMs), 1), 10000);
		setRunInterval(setInterval(() => {
			onClickClk(multiplayer);
		}, delayMs * multiplayer));
	}

	function onClickRun() {
		if (isNaN(runInterval)) {
			onClickClk();
			createRunInterval(delay);
		} else {
			clearInterval(runInterval);
			setRunInterval(NaN);
		}
	}

	function onChangeDelay(e) {
		let newDelay = Number(e.target.value);
		if (newDelay == 0 || isNaN(newDelay)) {
			newDelay = 1;
			setDelay("1");
		} else setDelay(e.target.value);

		if (!isNaN(runInterval)) {
			clearInterval(runInterval);
			createRunInterval(newDelay);
		}
	}

	return (
		<div>
			<div className="panel-container-row">
				<div className="panel-button" onClick={() => { onClickClk(); }}>Clock</div>
				<div className="panel-button" onClick={onClickRun}>{isNaN(runInterval) ? "Run" : "Stop"}</div>
			</div>
			<div className="panel-container-row">
				<div style={{ display: "inline-block" }}>delay:&nbsp;</div>
				<input className="cpu-delay-input" value={delay}
					onKeyPress={e => { if (!'.0123456789'.includes(e.key)) e.preventDefault(); }}
					onChange={onChangeDelay}>
				</input>
				<div style={{ display: "inline-block" }}>&nbsp;ms</div>
			</div>
			<div className="panel-container-row">
				<div className="panel-button" onClick={() => { clearInterval(runInterval); setRunInterval(NaN); cpu = createCPU(); }}>Reset All</div>
			</div>
			<div className="panel-container-row">
				<div className="grid-container">
					<div className="grid-item">s</div>
					<div className="grid-item" id="cpu-value-S">{cpu.reg.state.toString().padStart(5, " ")}</div>
					<div className="grid-item">i</div>
					<div className="grid-item" id="cpu-value-I">{("x" + cpu.reg.inst.toString(16)).padStart(5, " ")}</div>
					<div className="grid-item">#</div>
					<div className="grid-item" id="cpu-value-#">{cpu.reg.param.toString().padStart(5, " ")}</div>
					<div className="grid-item">c</div>
					<div className="grid-item" id="cpu-value-c">{cpu.reg.c.toString().padStart(5, " ")}</div>
					<div className="grid-item">x</div>
					<div className="grid-item" id="cpu-value-x">{cpu.reg.x.toString().padStart(5, " ")}</div>
					<div className="grid-item">y</div>
					<div className="grid-item" id="cpu-value-y">{cpu.reg.y.toString().padStart(5, " ")}</div>
					<div className="grid-item">M0</div>
					<div className="grid-item" id="cpu-value-M0">{cpu.reg.M[0].toString().padStart(5, " ")}</div>
					<div className="grid-item">M1</div>
					<div className="grid-item" id="cpu-value-M1">{cpu.reg.M[1].toString().padStart(5, " ")}</div>
					<div className="grid-item">M2</div>
					<div className="grid-item" id="cpu-value-M2">{cpu.reg.M[2].toString().padStart(5, " ")}</div>
					<div className="grid-item">M3</div>
					<div className="grid-item" id="cpu-value-M3">{cpu.reg.M[3].toString().padStart(5, " ")}</div>
					<div className="grid-item">M4</div>
					<div className="grid-item" id="cpu-value-M4">{cpu.reg.M[4].toString().padStart(5, " ")}</div>
					<div className="grid-item">M5</div>
					<div className="grid-item" id="cpu-value-M5">{cpu.reg.M[5].toString().padStart(5, " ")}</div>
					<div className="grid-item">M6</div>
					<div className="grid-item" id="cpu-value-M6">{cpu.reg.M[6].toString().padStart(5, " ")}</div>
					<div className="grid-item">M7</div>
					<div className="grid-item" id="cpu-value-M7">{cpu.reg.M[7].toString().padStart(5, " ")}</div>
				</div>
			</div>
		</div>
	);
}